
.header {
    width: 95vw;
    height: 50px;
    top: 2vh;
    left: 0;
    position: absolute;
    background-color: transparent;
    display: flex;
    flex-flow: row-reverse;
    gap: 2vw;
}

.header a {
    text-decoration: none;
    color: var(--cream);
    font-size: 2vh;
    font-weight: lighter;
    transition: color .1s;
}

.header a:hover{
    color: var(--green_highlight);
}

.landing {
    background-color: var(--green_2);
    width: 100%;
    height: 100vh;
}

.landing_main_content {
    padding-left: 20vw;
    padding-top: 30vh;
    width: 40%;
}

.landing_main_content > p:nth-child(1) {font-size: 1vw;}
.landing_main_content > p:nth-child(2) {font-size: 4vw;}
.landing_main_content > p:nth-child(3) {font-size: 3.2vw;}
.landing_main_content > p:nth-child(4) {font-size: 1.1vw;}

.landing_links {
    padding-top: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
}

.landing_links a {
    width: fit-content;
    height: fit-content;
    padding: 6px;
}


.projects {
    width: 100%;
    height: max-content;
    background-color: var(--cream);
    padding-bottom: 20vh;
}

.section_title {
    padding-left: 20vw;
    padding-top: 5vh;
    width: 40%;
}

.section_title > p:nth-child(1) {font-size: 2.5vw;}
.section_title > p:nth-child(2) {font-size: 1vw;}

.project_manager {
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 10vh;
}

.carousel_dots {
    width: 20%;
    height: fit-content;
    border-radius: .5vw;
    background-color: var(--green_5);
}

.carousel_dot {
    width: .5vw;
    height: .5vw;
    border-radius: .2vw;
    position: relative;
    background-color: var(--green_4);
    transition: .2s ease;
}

.project_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 80vw;
    height: 50vh;
}

.project_info_container {
    display: flex;
    max-width: 80%;
}

.project_image {
    max-width: 35vw;
    max-height: 40vh;
    border-radius: 10px;
    filter: drop-shadow(0px 0px 10px var(--dark_gray));
}

.project_description {
    width: 30vw;
    align-self: stretch;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.project_description > p {padding-left: 2.5vw; font-size: 4vh;}

.project_description_blurb {
    font-weight: bold;
    font-size: .8vw;
    background-color: var(--green_4);
    padding: 20px;
    border-radius: 10px;
    margin-left: -2vw;
    position: relative;
    height: fit-content;
}

.project_technologies {
    display: flex;
    gap: 2vw;
    margin-left: 3vw;
    font-weight: bold;
    font-size: 1vw;
}

.project_links {
    display: flex;
    margin-left: 3vw;
}
.project_links a {
    color: var(--green_2);
}

.left_arrow, .right_arrow {
    margin: 30px;
    transition: .15s ease-in-out;
    width: 2vw;
    min-width: 2vw;
}

.left_arrow:hover, .right_arrow:hover {
    transform: translateY(-5px);
}

.left_arrow img, .right_arrow img {
    width: 100%;
}

.about {
    height: fit-content;
    padding-bottom: 4vh;
    background-color: var(--green_2);
}

.about_container {
    display: flex;
    padding-left: 20vw;
    padding-top: 6vh;
}

.about_vertical_line {
    margin-bottom: 4vh;
    border-left: 1px solid var(--green_highlight);
}

.about_main_content {
    display: flex;
    flex-direction: column;
    max-width: 60%;
    margin-top: 4vh;
    white-space: pre-line;
}

.about_me_blurb > p {
    font-weight: bold;
    font-size: 2vw;
    color: var(--green_5);
    margin-left: 2vw;
}

.about_me_blurb > div {
    font-size: .8vw;
    color: var(--green_4);
    margin-left: 1vw;
    padding: 1vw;
}

.about_me_blurb > div > p {
    border-left: 1px solid var(--green_highlight);
    padding: 1vw;
}


.contact {
    background-color: var(--cream);
    height: fit-content;
    padding-bottom: 10vh;
}

.contact_button_container {
    width: 100%;
    height: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
}

.contact_button {
    margin-top: 10vh;
    padding: 3vh 8vh 3vh 8vh;
    background-color: transparent;
    border: 4px solid var(--green_highlight);
    border-radius: 2.5vw;
    color: var(--green_highlight);
    font-size: 2vh;
    transition: .2s;
}

.contact_button:hover {
    transform: translateY(-1vh);
    color: var(--cream);
    background-color: var(--green_highlight);
}

.footer {
    padding: 3vw;
    display: flex;
    justify-content: center;
    color: var(--cream);
    background-color: var(--green_2);
    font-size: 1.5vh;
}


@media only screen and (max-width: 1000px) {
    .landing_main_content {
        padding-left: 10vw;
        padding-right: 10vw;
        width: fit-content;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .landing_main_content > p:nth-child(1) {font-size: 4vw;}
    .landing_main_content > p:nth-child(2) {font-size: 8vw;}
    .landing_main_content > p:nth-child(3) {font-size: 6vw;}
    .landing_main_content > p:nth-child(4) {font-size: 3vw; margin-top: 3vh;}

    .section_title {
        display: flex;
        flex-direction: column;
        padding-left: 5vw;
        padding-right: 5vw;
        width: fit-content;
        align-items: center;
    }

    .section_title > p:nth-child(1) {font-size: 2.5vh;}
    .section_title > p:nth-child(2) {font-size: 1.5vh;}

    .project_manager {
        padding-top: 10vh;
        height: fit-content;
        margin: 0;
    }

    .projects {
        padding-bottom: 10vh;
    }

    .project_container {
        padding: 0;
        height: fit-content;
    }

    .project_info_container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2vh;
    }

    .project_image {
        max-width: 70vw;
        max-height: 30vh;
    }

    .left_arrow, .right_arrow {
        margin: 0;
        width: 5vw;
    }

    .project_description > p {padding: 0; text-align: center;}
    .project_description_blurb {padding: 0; margin: 0;}
    .project_technologies {padding: 0; margin: 0; font-size: 2vw;}
    .project_description {
        align-self: center;
        align-items: center;
        width: 100%;
        gap: 2vh;
    }

    .project_description_blurb {
        padding: 2vw;
        font-size: 2vh;
    }

    .carousel_dots {
        margin-top: 4vh;
    }

    .about_vertical_line {display: none}
    .about_container {
        justify-content: flex-start;
        padding: 0 4vw 0 4vw;
    }

    .about_main_content{width: 100%; max-width: none;}
    .about_me_blurb {
        width: 100%;
    }
    .about_me_blurb > p {
        font-size: 2vh;
    }

    .about_me_blurb > div > p {
        font-size: 1.6vh;
        padding-left: 3vh;
    }
}
